.list__text {
  max-width: 80%;
  display: block;
  overflow: hidden;
}

.list__checkbox-label {
  cursor: pointer;
  width: 20px;
  height: 20px;
  display: inline-block;
  position: absolute;
  right: 65px;
}

.list__checkbox-input {
  z-index: -2;
